<template>
  <div class="applicationinvoice">
    <HeadNav/>
    <div class="content">
      <IsHeadEmty/>
      <div class="applicationinvoicelist">
        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright applicationradio">
            <van-radio-group v-model="riseRadio">
              <van-radio name="1" @click="bankShow">
                <span></span>
                <img
                  slot="icon"
                  slot-scope="props"
                  :src="props.checked ? icon.active : icon.inactive"
                />
              </van-radio>
              <van-radio name="2" @click="bankShow">
                <span></span>
                <img
                  slot="icon"
                  slot-scope="props"
                  :src="props.checked ? icon.active : icon.inactive"
                />
              </van-radio>
            </van-radio-group>
          </div>
        </div>
        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="invoicePayable" placeholder="" />
          </div>
        </div>
        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright applicationradio">
            <van-radio-group v-model="riseRadio1">
              <van-radio name="1">
                <span></span>
                <img
                  slot="icon"
                  slot-scope="props"
                  :src="props.checked ? icon.active : icon.inactive"
                />
              </van-radio>
              <van-radio name="2">
                <span></span>
                <img
                  slot="icon"
                  slot-scope="props"
                  :src="props.checked ? icon.active : icon.inactive"
                />
              </van-radio>
            </van-radio-group>
          </div>
        </div>
        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="invoicePhone" placeholder="" />
          </div>
        </div>
        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="invoiceYx" placeholder="" />
          </div>
        </div>

        <div v-if="bankInfoShow" class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="identificationNumbe" placeholder="" />
          </div>
        </div>
        <div v-if="bankInfoShow" class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="bankOfDeposit" placeholder="" />
          </div>
        </div>
        <div v-if="bankInfoShow" class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="bankAccount" placeholder="" />
          </div>
        </div>

        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="invoiceadd" placeholder="" />
          </div>
        </div>
        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
            <van-field v-model="remarks" placeholder="" />
          </div>
        </div>
        <div class="applicationinvoiceitem pd16">
          <div class="applicationinvoiceleft">
            
          </div>
          <div class="applicationinvoiceright">
           <span class="colf25"> 2345.00</span>
          </div>
        </div>
      </div>
      <div class="sumbitbtn" @click="sumBit">
        
      </div>
    </div>
  </div>
</template>

<script>
    import HeadNav from "@/components/common/header";
    import IsHeadEmty from "@/components/common/isheademty";

    export default {
        name: "applicationinvoice",
        components: {
            HeadNav,
            IsHeadEmty
        },
        data() {
            return {
                riseRadio: "1",//
                riseRadio1: "1",//
                icon: {
                    active: "static/images/socialsecurity/checked.png",
                    inactive: "static/images/coverage/unselect.png"
                },
                remarks:'',//
                invoicePayable:'',//
                invoicePhone:'',//
                invoiceYx:'',//
                invoiceadd:'',//
                bankOfDeposit:'',//
                identificationNumbe:'',//
                bankAccount:'',//
                bankInfoShow:true,
            }
        },
        methods:{
            bankShow(){
                if(this.riseRadio1 == 1){
                    this.bankInfoShow = true
                }else {
                    this.bankInfoShow = false
                }
            },
          sumBit(){
          }
        }
    }
</script>

<style lang="scss" scoped>
  .content{
    background: #f2f2f2;
  }
  .sumbitbtn{
    width: 100%;
    height:2.45rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F97A2E;
    font-size: .9rem;
    color: #fff;
    position: fixed;
    bottom: 0;
  }
  .applicationinvoiceright {
    img {
      width: .65rem;
      height: .65rem;
    }
  }
  .applicationinvoicelist{
    height: calc(100vh - 2.3rem);
    overflow: auto;
    border-radius: .6rem .6rem 0 0;
    background: #fff;

    .applicationinvoiceitem{
      display: flex;
      align-items: center;
      height: 2.2rem;
      color: #666;
      font-size: .7rem;
      border-bottom: 1px solid #e6e6e6;
      .applicationinvoiceleft{
        width: 30%;
      }
      .applicationradio{
        .van-radio-group{
          display: flex;
        }
      }
      .applicationinvoiceright{
        width: 70%;
        .van-cell{
          padding-left: 0;
        }
        .van-radio-group{
          display: flex;
          /*justify-content: space-around;*/
          .van-radio:last-child{
            padding-left: 1rem;
          }
        }
      }

    }
    .applicationinvoiceitem:first-child{
      color: #333;
    }
  }
  .colf25{
    color: #F25454;
  }
</style>



