<template>
  <div class="schedulequery">
    <div class="content">
      <div class="back1">
        <van-icon name="arrow-left" @click="goupFn"/>
        <div class="contenttitle"></div>
      </div>
      <div class="schedulequeryhead">
        <img src="../../../../static/images/my/schedulequery/sq11.png" alt="">
      </div>
      <div class="stepbar">
        <div class="stepbaritem">
          <div class="stepbarleft">
            <img v-if="payinfo.status <=1" src="../../../../static/images/my/schedulequery/sq22.png" alt="">
            <img v-else src="../../../../static/images/my/schedulequery/sq1.png" alt="">
          </div>
          <div class="stepbarright">
            <div class="stepbartop">
              <span v-if="payinfo.status <1"></span>
              <span class="colf06" v-else></span>
              <span v-if="payinfo.payment_time !=0">{{payinfo.payment_time}}</span>
            </div>
            <div class="stepbarbot" v-if="payinfo.status <1">1-3</div>
            <div class="stepbarbot col666" v-else>1-3</div>
          </div>
        </div>
        <div class="stepbarline1 stepbarline"></div>
        <div class="stepbaritem">
          <div class="stepbarleft">
            <img v-if="payinfo.status <2" src="../../../../static/images/my/schedulequery/sq5.png" alt="">
            <img v-else-if="payinfo.status== 2" src="../../../../static/images/my/schedulequery/sq4.png" alt="">
            <img v-else-if="payinfo.status> 2" src="../../../../static/images/my/schedulequery/sq3.png" alt="">
          </div>
          <div class="stepbarright">
            <div class="stepbartop">
              <span v-if="payinfo.status <2"></span>
              <span class="colf06" v-else></span>
              <span v-if="payinfo.time1 !=0">{{payinfo.time1}}</span>
            </div>
            <div class="stepbarbot" v-if="payinfo.status <2">  </div>
            <div class="stepbarbot col666" v-else>  </div>
          </div>
        </div>
        <div class="stepbarline2 stepbarline"></div>
        <div class="stepbaritem">
          <div class="stepbarleft">
            <img v-if="payinfo.status <3" src="../../../../static/images/my/schedulequery/sq10.png" alt="">
            <img v-else-if="payinfo.status== 3" src="../../../../static/images/my/schedulequery/sq9.png" alt="">
            <img v-else-if="payinfo.status> 3" src="../../../../static/images/my/schedulequery/sq8.png" alt="">
          </div>
          <div class="stepbarright">
            <div class="stepbartop">
              <span v-if="payinfo.status <3"></span>
              <span class="colf06" v-else></span>
              <span v-if="payinfo.time2 !=0">{{payinfo.time2}}</span>
            </div>
            <div class="stepbarbot" v-if="payinfo.status <3"> 
              / </div>
            <div class="stepbarbot col666" v-else> 
              / </div>
          </div>
        </div>
        <div class="stepbarline3 stepbarline"></div>
        <div class="stepbaritem">
          <div class="stepbarleft">
            <img v-if="payinfo.status <4" src="../../../../static/images/my/schedulequery/sq6.png" alt="">
            <img v-else-if="payinfo.status >=4" src="../../../../static/images/my/schedulequery/sq7.png" alt="">
          </div>
          <div class="stepbarright">
            <div class="stepbartop">
              <span v-if="payinfo.status <4"></span>
              <span class="colf06" v-else></span>
              <span v-if="payinfo.time3 !=0">{{payinfo.time3}}</span>
            </div>
            <div class="stepbarbot" v-if="payinfo.status <4"> </div>
            <div class="stepbarbot col666" v-else> </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "schedulequery",
    data() {
      return{
        id:this.$route.query.id,
        payinfo:'',
      }
    },
    methods: {
      goupFn() {
        this.$router.go(-1)
      },
      getOrderDetail() {
        let that = this
        $.ajax({
          url: this.HOST + '/app/order/getOrderDetail ',
          type: "POST",
          data: {
            id: this.id
          },
          dataType: "JSON",
          success: function (r) {
            // that.$router.go(0)
            that.payinfo = r.data
            that.getLineHeight()

          }
        })
      },
      getLineHeight() {
        let stepbarline1 = Number($('.stepbaritem').eq(1).find('.stepbarleft').eq(0).offset().top) -
          Number($('.stepbaritem').eq(0).find('.stepbarleft').eq(0).offset().top)
        $('.stepbarline1').css({
          height: `calc(${stepbarline1}px)`,
          top: '2rem'
        })

        let stepbarline2 = Number($('.stepbaritem').eq(2).find('.stepbarleft').eq(0).offset().top) -
          Number($('.stepbaritem').eq(1).find('.stepbarleft').eq(0).offset().top)
        $('.stepbarline2').css({
          height: `calc(${stepbarline2}px)`,
          top: `calc(${stepbarline1}px + 2rem)`
        })
        let stepbarline3 = Number($('.stepbaritem').eq(3).find('.stepbarleft').eq(0).offset().top) -
          Number($('.stepbaritem').eq(2).find('.stepbarleft').eq(0).offset().top)
        $('.stepbarline3').css({
          height: `calc(${stepbarline3}px)`,
          top: `calc(${stepbarline2}px + ${stepbarline1}px + 2rem)`
        })
        switch (Number(this.payinfo.status)) {
          case 1:
            $('.stepbarline1').css({
              background:'#F97A2E'
            });
            break;
          case 2:
            $('.stepbarline1').css({
              background:'#F97A2E'
            });
            $('.stepbarline2').css({
              background:'#F97A2E'
            });
            break;
          case 3:
            $('.stepbarline1').css({
              background:'#F97A2E'
            });
            $('.stepbarline2').css({
              background:'#F97A2E'
            });
            $('.stepbarline3').css({
              background:'#F97A2E'
            });
          case 4:
            $('.stepbarline1').css({
              background:'#F97A2E'
            });
            $('.stepbarline2').css({
              background:'#F97A2E'
            });
            $('.stepbarline3').css({
              background:'#F97A2E'
            });
            break;
        }
        /*if( == ){
          $('.stepbarline1').css({
            /!*height: `calc(${stepbarline1}px)`,
            top: '2rem'*!/
            background:'red'
          })
        }*/
      }
    },
    mounted() {
      this.getOrderDetail()

    }
  }

</script>

<style lang="scss" scoped>
  .content{
    background: #fff;
  }
  .back1 {
    position: absolute;
    width: 100%;
    display: flex;
    align-items: center;
    color: #fff;
    z-index: 22;
    /*position: fixed;*/
    font-size: 1rem;
    height: 2.2rem;
  }

  .van-icon {
    position: absolute;
    left: 0.75rem;
  }

  .contenttitle {
    position: absolute;
    left: 50%;
    margin-left: -1.8rem;
    font-size: 0.9rem;
    color: #fff;
  }

  .schedulequeryhead {
    width: 100%;
    height: 7.55rem;

    img {
      width: 100%;
    }
  }

  .stepbar {
    padding: 1rem 1.25rem;
    position: relative;

    .stepbaritem {
      display: flex;
      position: relative;
      padding-top: 2rem;
      z-index: 99;

      .stepbarleft {
        width: 2.6rem;
        height: 2.6rem;

        img {
          width: 100%;
        }
      }

      .stepbarright {
        padding-left: 1rem;
        width: calc(100% - 2.6rem);

        .stepbartop {
          span:nth-child(1) {
            color: #666666;
            font-size: .7rem;
          }
          .colf06{
            color: #FF7006 !important;
          }
          span:nth-child(2) {
            font-size: .5rem;
            color: #666;
          }

        }

        .stepbarbot {
          font-size: .6rem;
          color: #B2B2B2;
        }
        .col666{
          color:#666666 !important;
        }
      }
    }

    .stepbaritem:nth-child(1) {
      padding: 0;
    }

    .stepbarline {
      width: .1rem;
      background: #D9D9D9;
      position: absolute;
      left: 2.55rem;
    }
  }
</style>



